<template>
  <v-container fluid>
    <loading :active.sync="isLoading"></loading>
    <v-card max-width="1000" class="form elevation-12">
      <!-- toolbar -->
      <v-toolbar cards flat color="teal lighten-4">
        <v-btn icon>
          <v-icon color="light-blue accent-3">edit</v-icon>
        </v-btn>
        <v-card-title class="title font-weight-regular">編輯表單</v-card-title>
      </v-toolbar>
      <!-- snake bar -->
      <v-snackbar v-model="snackbar" absolute top right color="success">
        <span>處理中...</span>
        <v-icon dark>check_circle</v-icon>
      </v-snackbar>
      <!-- form body -->
      <v-form ref="editForm" @submit.prevent="submit">
        <v-container grid-list-xl fluid px-5>
          <v-layout wrap>
            <v-flex xs12 sm6>
              <v-text-field v-model="form.alarmId" :rules="rules.name" color="blue darken-2" label="警報編號" required></v-text-field>
            </v-flex>
            <v-flex xs12 sm6>
              <v-text-field v-model="form.description" :rules="rules.name" color="blue darken-2" label="警報描述" required></v-text-field>
            </v-flex>
            <v-flex xs12>
              <v-textarea v-model="form.detail" color="blue darken-2" :rules="rules.name" required auto-grow>
                <div slot="label">
                  處理步驟
                </div>
              </v-textarea>
            </v-flex>
            <v-flex xs12>
              <label>
                <v-chip color="light-blue darken-4" text-color="white">
                  <v-avatar>
                    <v-icon>cloud_upload</v-icon>
                  </v-avatar>
                  上傳檔案
                </v-chip>
                <input type="file" id="file" ref="editFiles" style="display:none;" multiple @change="handleFileUpload">
                            </label>
                <v-chip outline label color="blue-grey darken-1" v-for="(file, index) in form.files" :key="index">
                  <v-avatar>
                    <v-icon color="blue-grey darken-1">attach_file</v-icon>
                  </v-avatar>{{ file.name }}
                </v-chip>
            </v-flex>
            <v-flex xs12>
              <v-card-actions>
                <v-btn @click="resetForm">重新填寫</v-btn>
                <v-spacer></v-spacer>
                <v-btn :disabled="!formIsValid" color="primary" @click="submit">送出</v-btn>
              </v-card-actions>
            </v-flex>
          </v-layout>
        </v-container>
      </v-form>
    </v-card>
  </v-container>
</template>

<script>
import axios from 'axios'
export default {
  data () {
    return {
      form: {},
      rules: {
        name: [val => (val || '').length > 0 || '此欄不能為空']
      },
      snackbar: false,
      defaultForm: {},
      isLoading: false
    }
  },

  computed: {
    formIsValid () {
      return this.form.alarmId && this.form.description && this.form.detail
    }
  },

  methods: {
    resetForm () {
      this.form = Object.assign({}, this.defaultForm)
      this.$refs.editFiles.value = ''
    },
    async submit () {
      try {
        this.isLoading = true
        this.snackbar = true
        let formData = new FormData()
        if (this.$refs.editFiles.files.length) {
          for (let i = 0; i < this.form.files.length; i++) {
            let file = this.form.files[i]
            formData.append('file', file)
          }
        }
        formData.append('alarmId', this.form.alarmId)
        formData.append('description', this.form.description)
        formData.append('detail', this.form.detail)

        await axios.put(
          `${window.location.origin}/sop/${this.$route.params.alarmId}`,
          formData,
          {
            headers: {
              'Content-Type': 'multipart/form-data',
              Authorization: `Bearer ${this.$store.getters['getUser'].token}`
            }
          }
        )
        this.isLoading = false
        this.$router.push('/sop')
      } catch (err) {
        this.$store.commit('setErrMessage', {
          errMessage:
            err.response.data.error.message || 'Something went wrong...',
          errStatus: true
        })
        this.isLoading = false
      }
    },
    handleFileUpload () {
      this.form.files = this.$refs.editFiles.files
    },
    async fetchData () {
      try {
        this.isLoading = true
        const res = await axios.get(
          `${window.location.origin}/sop/${this.$route.params.alarmId}`
        )
        if (res.data.length) {
          let dataBack = res.data[0]
          let { alarmId, description, detail } = dataBack
          if (dataBack.files) {
            var fileArray = JSON.parse(dataBack.files).dest
            var files = fileArray.map(file => {
              return {
                name: file.slice(8)
              }
            })
          }
          this.defaultForm = {
            alarmId,
            description,
            detail,
            files
          }
          this.resetForm()
          this.isLoading = false
        } else {
          this.$store.commit('setErrMessage', {
            errMessage: '查無資料',
            errStatus: true
          })
          this.isLoading = false
          this.$router.push('/sop')
        }
      } catch (err) {
        this.$store.commit('setErrMessage', {
          errMessage:
            err.response.data.error.message || 'Something went wrong...',
          errStatus: true
        })
        this.isLoading = false
      }
    }
  },
  created () {
    this.fetchData()
  }
}
</script>

<style lang="scss" scoped>
.form {
  min-height: 75vh;
  margin: 0 auto;
}
</style>
