import Vue from 'vue'
import Router from 'vue-router'
import Sop from './views/Sop.vue'
import SopDetail from '@/components/SopDetail.vue'
import SopList from '@/components/SopList.vue'
import Login from '@/components/Login.vue'
import SopForm from '@/components/SopForm.vue'
import SopEdit from '@/components/SopEdit.vue'
import store from '@/store'

Vue.use(Router)

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/sop',
      component: Sop,
      children: [
        {
          path: '',
          name: 'List',
          component: SopList
        },
        {
          path: 'new',
          name: 'Form',
          component: SopForm,
          beforeEnter: (to, from, next) => {
            if (store.state.isAuthorized) {
              next()
            } else {
              next('/')
            }
          }
        },
        {
          path: 'edit/:alarmId',
          name: 'Edit',
          component: SopEdit,
          beforeEnter: (to, from, next) => {
            if (store.state.isAuthorized) {
              next()
            } else {
              next('/')
            }
          }
        },
        {
          path: ':alarmId',
          component: SopDetail
        }
      ]
    },
    {
      path: '/',
      component: Login,
      beforeEnter: (to, from, next) => {
        if (store.state.isAuthorized) {
          next('/sop')
        } else {
          next()
        }
      }
    }
    // {
    //   path: '/about',
    //   name: 'about',
    //   // route level code-splitting
    //   // this generates a separate chunk (about.[hash].js) for this route
    //   // which is lazy-loaded when the route is visited.
    //   component: () => import(/* webpackChunkName: "about" */ './views/About.vue')
    // }
    // {
    //   path: '/sop',
    //   name: 'sop',
    //   component: Sop
    // }
  ]
})
