<template>
  <v-list subheader three-line class="list">
    <loading :active.sync="isLoading"></loading>
    <!-- header -->
    <v-layout row justify-start mp-4 align-center>
      <v-flex>
        <span class="display-2">SOP 列表</span>
        <v-btn flat icon color="primary" @click="fetchData">
          <v-icon>cached</v-icon>
        </v-btn>
      </v-flex>
      <v-spacer></v-spacer>
      <template v-if="isLogin">
        <v-btn flat color="primary" to="/sop/new">
          <v-icon left>add_circle_outline</v-icon>新增表單
        </v-btn>
      </template>
    </v-layout>

    <!-- list loop -->
    <template v-for="(sop, index) in sqlData">
      <v-list-tile :key="sop.alarmId" :to="`/sop/${sop.alarmId}`">
        <v-list-tile-content>
          <v-list-tile-title>{{ sop.alarmId }}</v-list-tile-title>
          <v-list-tile-sub-title>
            {{ sop.description }}
          </v-list-tile-sub-title>
        </v-list-tile-content>
      </v-list-tile>
      <v-divider :key="index"></v-divider>
    </template>
  </v-list>
</template>
<script>
import axios from 'axios'

export default {
  data () {
    return {
      sqlData: [],
      isLoading: false
    }
  },
  methods: {
    async fetchData () {
      try {
        this.isLoading = true
        const res = await axios.get(`${window.location.origin}/sop/list`)
        this.sqlData = res.data
        this.isLoading = false
      } catch (err) {
        this.$store.commit('setErrMessage', {
          errMessage: '資料庫無回應，請確認網路連線。',
          errStatus: true
        })
        this.isLoading = false
      }
    }
  },
  computed: {
    isLogin () {
      return this.$store.getters['getUser'].isAuthorized
    }
  },
  created () {
    this.fetchData()
  }
}
</script>

<style lang="scss" scoped>
.list {
  background-color: rgb(250, 250, 250);
}
</style>
