<template>
  <v-container fluid>
    <loading :active.sync="isLoading"></loading>
    <div class="content elevation-12">
      <v-layout align-center>
        <v-flex v-if="isLogin">
          <v-btn flat color="primary" :to="`/sop/edit/${params}`">編輯<v-icon right>create</v-icon>
          </v-btn>
          <v-btn flat color="error" @click="deleteSop">刪除<v-icon right>delete</v-icon>
          </v-btn>
        </v-flex>
        <v-spacer></v-spacer>
        <v-flex :class="{read: !isLogin}">
          <p class="text-xs-right title font-weight-black sop__wording">STANDARD OPERATING PROCEDURE</p>
        </v-flex>
      </v-layout>

      <v-layout row wrap class="content__table" mb-4>
        <v-flex xs3 class="content__item content__item--th">
          <span class="content__item--title">警報編號</span>
        </v-flex>
        <v-flex xs3 class="content__item content__item--th">
          <span class="content__item--title">描述</span>
        </v-flex>
        <v-flex xs3 class="content__item content__item--th">
          <span class="content__item--title">附件數量</span>
        </v-flex>
        <v-flex xs3 class="content__item content__item--th">
          <span class="content__item--title">建立日期</span>
        </v-flex>
        <v-flex xs3 class="content__item">
          <span class="break">{{ sqlData.alarmId }}</span>
        </v-flex>
        <v-flex xs3 class="content__item">
          <span class="break">{{ sqlData.description }}</span>
        </v-flex>
        <v-flex xs3 class="content__item">
          <span class="break">{{ sqlData.files ? sqlData.files.length : '0' }}</span>
        </v-flex>
        <v-flex xs3 class="content__item">
          <span class="break">{{ sqlData.date }}</span>
        </v-flex>
      </v-layout>

      <div class="procedure">
        <p class="subheading font-weight-bold">處理流程</p>
        <v-list two-line>
          <template v-for="(step, index) in sqlData.detail">
            <v-list-tile :key="index">
              <!-- check box -->
              <!-- <v-list-tile-action>
                <v-checkbox></v-checkbox>
              </v-list-tile-action> -->

              <v-list-tile-content>
                <!-- <v-list-tile-title class="break">{{step}}</v-list-tile-title> -->
                <!-- <v-text-field label="Solo" solo>{{step}}</v-text-field> -->
                <v-checkbox :label="step" color="success" light></v-checkbox>
              </v-list-tile-content>
            </v-list-tile>
          </template>
        </v-list>
      </div>
      <div class="appendix">
        <p class="subheading font-weight-bold">檔案附件</p>
        <v-layout row wrap>
          <template v-for="(file, index) in sqlData.fileName">
            <v-flex xs12 sm4 md3 :key="index">
              <div class="text-xs-center">
                <v-chip outline color="blue-grey darken-1">
                  <a :href="`${baseUrl}uploads/${file}`" :download="file" class="appendix__download">
                    {{ file.length > 10 ? `${file.substring(0, 10)}...` : file}}
                    <v-avatar>
                      <v-icon color="blue-grey darken-1" right>cloud_download</v-icon>
                    </v-avatar>
                  </a>
                </v-chip>
              </div>
            </v-flex>
          </template>
        </v-layout>
      </div>
    </div>
  </v-container>
</template>

<script>
import axios from "axios";
export default {
  data() {
    return {
      params: this.$route.params.alarmId,
      isLoading: false,
      baseUrl: process.env.BASE_URL,
      sqlData: {}
    };
  },
  methods: {
    async fetchData() {
      try {
        this.isLoading = true;
        const res = await axios.get(`${window.location.origin}/sop/${this.params}`);
        if (res.data.length) {
          let dataBack = res.data[0];
          let { alarmId, description } = dataBack;
          let detail = dataBack.detail.split("\n");
          let time = new Date(Number(dataBack.date));
          let date = `${time.getFullYear()}/${time.getMonth() +
            1}/${time.getDate()}`;
          if (dataBack.files) {
            var files = JSON.parse(dataBack.files).dest;
            var fileName = files.map(file => {
              return file.slice(8);
            });
          }
          this.sqlData = {
            alarmId,
            description,
            detail,
            files,
            fileName,
            date
          };
          this.isLoading = false;
        } else {
          this.$store.commit("setErrMessage", {
            errMessage: "查無資料",
            errStatus: true
          });
          this.isLoading = false;
          this.$router.push("/sop");
        }
      } catch (err) {
        this.$store.commit("setErrMessage", {
          errMessage:
            err.response.data.error.message || "Something went wrong...",
          errStatus: true
        });
        this.isLoading = false;
      }
    },
    async deleteSop() {
      try {
        if (
          window.confirm(`確定要刪除SOP編號${this.params}嗎？\n刪除後無法復原`)
        ) {
          this.isLoading = true;
          await axios.delete(`${window.location.origin}/sop/${this.params}`, {
            headers: {
              Authorization: `Bearer ${this.$store.getters["getUser"].token}`
            }
          });
          this.$store.commit("setErrMessage", {
            errMessage: null,
            errStatus: false
          });
          this.isLoading = false;
          this.$router.push("/sop");
        } else {
          this.isLoading = false;
          return;
        }
      } catch (err) {
        this.$store.commit("setErrMessage", {
          errMessage: err.response.data.error.message,
          errStatus: true
        });
        this.isLoading = false;
        this.$router.push("/");
      }
    }
  },
  computed: {
    isLogin() {
      return this.$store.getters["getUser"].isAuthorized;
    }
  },
  created() {
    this.fetchData();
  }
};
</script>

