import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    user: null,
    isAuthorized: false,
    token: null,
    errMessage: null,
    error: false
  },
  mutations: {
    setUser (state, payload) {
      state.user = payload.username
      state.isAuthorized = payload.isAuthorized
      state.token = payload.token
    },
    setErrMessage (state, payload) {
      state.errMessage = payload.errMessage
      state.error = payload.errStatus
    },
    setErrStatus (state, payload) {
      state.error = payload
    }
  },
  getters: {
    getUser (state) {
      return {
        user: state.user,
        isAuthorized: state.isAuthorized,
        token: state.token
      }
    },
    getErrMessage (state) {
      return state.errMessage
    },
    getErrStatus (state) {
      return state.error
    }
  },
  actions: {
    async signin ({ commit }, payload) {
      try {
        const res = await axios.post(`${window.location.origin}/user/signin`, payload);
        commit('setUser', {
          username: res.data.username,
          isAuthorized: true,
          token: res.data.token
        })
        commit('setErrMessage', {
          errMessage: null,
          errStatus: false
        })
      } catch (err) {
        commit('setUser', { username: null, isAuthorized: false, token: null })
        commit('setErrMessage', {
          errMessage: err.response.data.error.message,
          errStatus: true
        })
      }
    }
  }
})
