<template>
  <v-snackbar v-model="snackbar" multi-line top right :timeout="3000" color="blue-grey darken-1">
    {{ errMessage }}
  </v-snackbar>
</template>

<script>
export default {
  data () {
    return {
      snackbar: false
    }
  },
  computed: {
    errMessage () {
      return this.$store.getters['getErrMessage']
    },
    errStatus () {
      return this.$store.getters['getErrStatus']
    }
  },
  watch: {
    errStatus: function (after, before) {
      if (after) {
        this.snackbar = true
        this.$store.commit('setErrStatus', false)
      }
    }
  }
}
</script>

