<template>
  <v-container fluid fill-height>
    <loading :active.sync="isLoading"></loading>
    <v-layout align-center justify-center>
      <v-flex xs12 md8 lg6>
        <v-card class="elevation-12">
          <v-toolbar dark color="indigo">
            <v-toolbar-title>管理者登入</v-toolbar-title>
          </v-toolbar>
          <v-card-text>
            <v-form>
              <v-text-field prepend-icon="person" name="login" label="帳號" type="text" v-model="username"></v-text-field>
              <v-text-field id="password" prepend-icon="lock" name="password" label="密碼" type="password" v-model="password"></v-text-field>
            </v-form>
          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="primary" @click="submit">登入</v-btn>
          </v-card-actions>
        </v-card>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
import axios from 'axios'
export default {
  data () {
    return {
      username: '',
      password: '',
      isLoading: false
    }
  },
  methods: {
    async submit () {
      try {
        this.isLoading = true
        let payload = {
          username: this.username,
          password: this.password
        }
        const res = await axios.post(
          `${window.location.origin}/user/signin`,
          payload
        )
        this.$store.commit('setUser', {
          username: res.data.username,
          isAuthorized: true,
          token: res.data.token
        })
        this.$store.commit('setErrMessage', {
          errMessage: '登入成功',
          errStatus: true
        })
        this.isLoading = false
        this.$router.push('/sop')
      } catch (err) {
        this.$store.commit('setUser', {
          username: null,
          isAuthorized: false,
          token: null
        })
        this.$store.commit('setErrMessage', {
          errMessage:
            err.response.data.error.message || 'Something went wrong...',
          errStatus: true
        })
        this.isLoading = false
      }
    }
  }
}
</script>

